import React, { useEffect, useState } from 'react'
import '../ComponentsCSS/userinstantinfo.css'
import { useNavigate } from 'react-router-dom'
import { get, ref } from 'firebase/database';
import { db } from '../firebase';

const UserInstantDetails = ({instant, userId}) => {
    const [userDetails, setUserDetails] = useState(null);

    
  useEffect(() => {
    if (userId) {
      // Fetch user details based on the userId
      const userRef = ref(db, `users/${userId}`);
      get(userRef)
        .then((snapshot) => {
          if (snapshot.exists()) {
            const user = snapshot.val();
            setUserDetails(user);
          }
        })
        .catch((error) => {
          console.error('Error fetching user details:', error);
        });
    }
  }, [userId]);

    const handleCloseInstant = ()=>{
        instant(false)
    }
    


    const navigate = useNavigate()
    const uid = localStorage.getItem('uid')


  return (
    <div className="loading-overlay">
            <div className='user-instant-details'>
                <div className="touch-user">
                    <img src={userDetails?.profiles?.profileURL } alt="" className='instant-user-profile' />
                    <div className="user-details-instant">
                        <h1>{userDetails?.profilename}</h1>
                        <p>@{userDetails?.username}</p>
                    </div>
                    <div className="user-main-details">
                        <p><span className='header-instant'>VM no </span>: {userDetails?.vm_no} </p>
                        <p><span className='header-instant'>Reg no </span>: {userDetails?.reg_no} </p>
                        <p><span className='header-instant'>Department </span>:{userDetails?.department}</p>
                        <p><span className='header-instant'>Year </span>: {userDetails?.year} Year</p>
                        <p><span className='header-instant'>College </span>: Vel Tech Multi Tech Dr. Rangarajan Dr.Sakunthala Engineering College</p>
                        <div className="buttons-instant">
                            <button onClick={handleCloseInstant}>Close</button>
                        </div>

                    </div>
                </div>
                
                </div>
    </div>

  )
}

export default UserInstantDetails