import React from 'react'
import ProfileFolio from './ProfileFolio'
import Blogs from './Blogs'
import ConnectMe from './ConnectMe'
import ProfileFolioContainer from './ProfileFolioContainer'

const ProfileBottom = ({user, selectedButton}) => {

  
  return (
<>
        {selectedButton==="ProfileFolio" && <ProfileFolioContainer user={user}/>}
        {selectedButton === "Blogs" && <Blogs user={user}/>}
        {selectedButton === "ConnectMe" && <ConnectMe user={user}/>}
</>
  )
}

export default ProfileBottom