import React from 'react';
import '../ComponentsCSS/header.css'
import VTMTLogo from '../Assets/logo.png'


const Header = () => {
  const uid = localStorage.getItem('uid')
    

  return (
    <div className='header'>
        <img src={VTMTLogo} alt="" className='vtmt-logo'/>
        {window.location.pathname === '/home' ? (
        <img
          src="https://cdn-icons-png.flaticon.com/128/1384/1384023.png"
          className='whatsapp-noti'
          alt=""
          onClick={() => {
            window.location.href = `/whatsappnoti`;
          }}
        />
      ) : 
      window.location.pathname === '/profile' ? (
        <img
          src="https://cdn-icons-png.flaticon.com/128/2040/2040504.png"
          className='whatsapp-noti'
          alt=""
          onClick={() => {
            window.location.href = `/editinfo`;
          }}
        />
      ) : ''}    
      </div>
  )
}

export default Header