import React, { useState, useEffect } from 'react';
import AOIBox from './AOIBox';
import SkillBox from './SkillBox';
import { db } from '../firebase';
import { get, ref } from 'firebase/database';
import ProfileFolio from './ProfileFolio';
import CreateProfileFolio from './CreateProfileFolio';
import NoProfileFolio from './NoProfileFolio';

const ProfileFolioContainer = ({ user , uid }) => {
  const profileRef = ref(db, `users/${user.uid}/profilefolio`);
  const userUID = localStorage.getItem('username');

  const [profileData, setProfileData] = useState(null);

  useEffect(() => {
    get(profileRef)
      .then((snapshot) => {
        if (snapshot.exists()) {
          // Data exists, set it in state
          setProfileData(snapshot.val());
        }
      })
      .catch((error) => {
        console.error('Error fetching profile data:', error);
        // Handle any errors here
      });
  }, [profileRef]);

  return (
    <div className="profilefolio-container" >
      {profileData && user.username !== userUID ? (
        <ProfileFolio data={profileData} />
      ) : !profileData && user.username === userUID ? (
        <CreateProfileFolio user={user} />
      ) : !profileData && user.username !== userUID ? (
        <NoProfileFolio />
      ) : (
        <ProfileFolio data={profileData}  />
      )}
    </div>
  );
};

export default ProfileFolioContainer;
