import React, { useEffect, useState } from 'react'
import 'firebase/database'
import { get, push, ref, remove, update } from 'firebase/database'
import { db } from '../firebase'
import Timestamp from './Timestamp'
import { set } from 'date-fns'
import { toast } from 'react-toastify'

const NotiBox = ({noti}) => {
    const [user, setUser] = useState([])
    const [post, setPost ] = useState([])
    const [myUser, setMyUser] = useState([])
    const uid = localStorage.getItem('uid')
    

    useEffect(()=>{
        const userRef = ref(db, `users/${noti?.uid}/`)
        get(userRef)
        .then((snapshot)=>{
            if(snapshot.exists()){
                setUser(snapshot.val())
            }
        })
        const myUserRef = ref(db, `users/${uid}/`)
        get(myUserRef)
        .then((snapshot)=>{
            if(snapshot.exists()){
                setMyUser(snapshot.val())
            }
        })

        const notiRef = ref(db,`notification/${localStorage.getItem('uid')}/${noti?.key}`);
        

        const postRef = ref(db, `posts/${noti?.post}`)
        get(postRef)
        .then((snapshot)=>{
            if(snapshot.exists()){
                setPost(snapshot.val())
            }
        })
    })

    const handlePostNav = () => {
        if(noti?.post){
            window.location.href = `/post/${noti?.post}`
        }else{
            window.location.href = `profile/${noti?.uid}`
        }
    }

    const handleAccept = () => {
        const notiRef = ref(db, `notification/${user?.uid}/`)
        const whatsappRef = ref(db, `whatsapp-permission/${user?.uid}/${uid}`)
        const whatsappNoti = ref(db,`whatsapp-noti/${uid}/${noti?.key}`)


        const myNoti = ref(db,`notification/${uid}/${noti?.key}`)


        const notify = push(notiRef,{
            uid: uid,
            message: `${myUser?.profilename} has accepted your request! Chat Now `,
            accepted: true,
            timestamp: Date.now()
        })
        const notiKey = notify.key

        update(ref(db, `notification/${user?.uid}/${notiKey}`),{
            uid: uid,
            message: `${myUser?.profilename} has accepted your request! Chat Now `,
            accepted: true,
            timestamp: Date.now(),
            key: notiKey
        })

        set(ref(db, `whatsapp-noti/${user?.uid}/${notiKey}`),{
            uid: uid,
            message: `${myUser?.profilename} has accepted your request! Chat Now `,
            accepted: true,
            timestamp: Date.now(),
            key: notiKey
        })

        

        push(ref(db,`notification/${uid}`),{
            uid: user?.uid,
            message: `You have accepted the WhatsaApp Request from ${user?.profilename}. You can chat with them in WhatsApp!`,
            timestamp: Date.now(),
            accepted:true,
        })

        push(ref(db,`whatsapp-noti/${uid}`),{
            uid: user?.uid,
            message: `You have accepted the WhatsaApp Request from ${user?.profilename}. You can chat with them in WhatsApp!`,
            timestamp: Date.now(),
            accepted:true,
        })

        update(whatsappRef,{
            acception: true
        })

        remove(myNoti)
        remove(whatsappNoti)
        window.location.reload()

        toast.success(`You can now chat with ${user?.profilename}`)


        
    }

    const handleReject = () => {
        const notiRef = ref(db, `notification/${user?.uid}/`)
        const myNoti = ref(db,`notification/${uid}/${noti?.key}`)
        const whatsappNoti = ref(db,`whatsapp-noti/${uid}/${noti?.key}`)
        const whatsappRef = ref(db, `whatsapp-permission/${user?.uid}/${uid}`)
        

        const notify = push(notiRef,{
            uid: uid,
            message: ` rejected your request!`,
            read : false,
            timestamp: Date.now()
        })

        const notiKey = notify.key

        set(ref(db, `notification/${user?.uid}/${notiKey}`),{
            uid: uid,
            message: ` rejected your request!`,
            read : false,
            timestamp: Date.now(),
            key : notiKey
        })

        set(ref(db, `whatsapp-noti/${user?.uid}/${notiKey}`),{
            uid: uid,
            message: ` rejected your request!`,
            read : false,
            timestamp: Date.now(),
            key : notiKey
        })

        push(ref(db,`notification/${uid}`),{
            uid: user?.uid,
            message: `You have rejected the WhatsaApp Request from ${user?.profilename}. If it was by mistake, ask ${user?.profilename} to resend te request from ConnectMe section!`,
            timestamp: Date.now(),
            read : false,
        })

        set(ref(db,`whatsapp-noti/${uid}`),{
            uid: user?.uid,
            message: `You have rejected the WhatsaApp Request from ${user?.profilename}. If it was by mistake, ask ${user?.profilename} to resend te request from ConnectMe section!`,
            timestamp: Date.now(),
            read : false,
        })

        update(whatsappRef,{
            acception: false
        })

        toast.success(`You rejected ${user?.profilename}'s whatsapp request `)


        remove(myNoti)
        remove(whatsappNoti)
        window.location.reload()


    }




  return (
    <>
    { <div className={window.location.pathname === '/notification' ? 'noti-box-with-com' : window.location.pathname === '/whatsappnoti' ? 'whatsapp-notibox' : ''} >
    <div className='noti-box' onClick={handlePostNav}>
        <img src={user?.profiles?.profileURL} alt=""  className='noti-profile' onClick={()=>{window.location.href=`/profile/${noti?.uid}`}}/>
        <p style={{fontSize:'14px'}}> <span onClick={handlePostNav}>{user?.profilename}</span> {noti?.message}</p>
        {noti?.post &&  post?.media && <img src={post?.media} alt="" className='noti-post-pic' />}
    </div>
    {noti?.comment && <p className='comment-noti'>{noti?.comment}</p>}
    {noti?.whatsapp && 
    <div className='whatsapp-button-container'>
        <button onClick={handleAccept} style={{background:'#8EFF84'}}> Accept</button>
        <button onClick={handleReject} style={{background:'#FFACAC'}}>Reject</button>
    </div>
    }
    {noti?.accepted === true  && 
    <div className='whatsapp-button-container'>
        <button onClick={()=>{window.location.href = `https://api.whatsapp.com/send/?phone=%2B91${user?.socialmedias?.whatsapp}&text&type=phone_number&app_absent=0`}} style={{background:'#8EFF84'}}>Chat Now</button>
    </div>
    }
    {noti?.timestamp && <Timestamp timestamp={noti?.timestamp}/>}

    </div>}
    </>
  )
}

export default NotiBox