import React from 'react'
import PortfolioPNG from '../Assets/create-profilefolio.png'
import { useNavigate } from 'react-router-dom'

const CreateProfileFolio = () => {
  const navigate = useNavigate()
  const uid = localStorage.getItem('uid')




  return (
    <div className='create-profile-folio'>
        <h1>Create your ProfileFolio section within seconds!</h1>
        <img src={PortfolioPNG} alt="" />

        <button onClick={()=>{navigate(`/createprofilefolio/${uid}`)}}>
            <img src="" alt="" />
            Create
        </button>
    </div>
  )
}

export default CreateProfileFolio