import React from 'react'
import RecoBtn from '../Assets/reco-btn.png'

const AOIBox = ({data}) => {
  return (
    <div className="aoi-box">
          <div className="aoi-info">
            <img src={data.url} alt="" />
            <h1>{data.head}</h1>
          </div>
          <div className="aoi-reco">
            <img src={RecoBtn} alt="" />
            <p>198</p>
          </div>
    </div>
  )
}

export default AOIBox