import React from 'react'

const AddComment = ({handleCommentInput , handleCommentUpload , commentBox}) => {
  return (
    <div className='comment-input'>
      <img src="https://cdn-icons-png.flaticon.com/128/507/507257.png" alt="" onClick={()=>{commentBox(false)}} />
        <input type="text" placeholder='add comment...' onChange={handleCommentInput}/>
        <img src="https://cdn-icons-png.flaticon.com/128/10322/10322482.png" onClick={handleCommentUpload} alt="" />
    </div>
  )
}

export default AddComment