import { get, ref, update, remove, push } from 'firebase/database';
import React, { useState, useEffect } from 'react';
import ProfileBottom from './ProfileBottom';
import { db, auth } from '../firebase';
import { toast } from 'react-toastify';
import UserInstantDetails from './UserInstantDetails';
import { set } from 'date-fns';

const ProfileTop = ({ user, uid }) => {
  const [blogsCount, setBlogsCount] = useState(0);
  const [followersCount, setFollowersCount] = useState(0);
  const [followers, setFollowers] = useState([]);
  const [following, setFollowing] = useState([]);
  const [followingCount, setFollowingCount] = useState(0);
  const [showInstantInfo, setShowInstantInfo] = useState(false); 
  const [follow, setFollow] = useState(false);
  const [userUid, setUserUid] = useState('');
  const [profileURL, setProfileURL] = useState("https://static-00.iconduck.com/assets.00/profile-circle-icon-512x512-zxne30hp.png");
  const [selectedButton, setSelectedButton] = useState("ProfileFolio");
  const targetUserUID = uid;
  const myUsername = localStorage.getItem('username');
  const myUID = localStorage.getItem('uid')


  const currentUser = auth.currentUser;

  
  useEffect(() => {
    // Fetch and update the counts for personal profile
    if (myUID) {
      // Fetch blogs count
      const postRef = ref(db,`users/${myUID}/posts`)
      get(postRef)
      .then((snapshot)=>{
        if(snapshot.exists()){
          const posts = snapshot.val()
          setBlogsCount(Object.keys(posts).length)
        }
      })
  
      // Fetch followers count
      const followersRef = ref(db, `users/${myUID}/followers`);
      get(followersRef)
        .then((snapshot) => {
          if (snapshot.exists()) {
            const followers = snapshot.val();
            setFollowers(Object.keys(followers))
            const count = Object.keys(followers).length;
            setFollowersCount(count);
          } else {
            setFollowersCount(0);
          }
        });
  
      // Fetch following count
      const followingRef = ref(db, `users/${myUID}/following`);
      get(followingRef)
        .then((snapshot) => {
          if (snapshot.exists()) {
            const following = snapshot.val();
            setFollowing(Object.keys(following))
            const count = Object.keys(following).length;
            setFollowingCount(count);
          } else {
            setFollowingCount(0);
          }
        });
    }
  }, [myUID]);
  



  useEffect(() => {
    if (user) {
      // Fetch the profile URL when user data is available
      const URLRef = ref(db, `users/${user.uid}/profiles/profileURL`);

      get(URLRef)
        .then((snapshot) => {
          if (snapshot.exists()) {
            const profileURL = snapshot.val();
            setProfileURL(profileURL);
          } else {
            setProfileURL("https://static-00.iconduck.com/assets.00/profile-circle-icon-512x512-zxne30hp.png");
          }
        });
    }

   
  }, [user, targetUserUID, userUid]);

  useEffect(() => {
    if (user && myUID && targetUserUID) {
      // Fetch blogs count
      const postRef = ref(db,`users/${user?.uid}/posts`)
      get(postRef)
      .then((snapshot)=>{
        if(snapshot.exists()){
          const posts = snapshot.val()
          setBlogsCount(Object.keys(posts).length)
        }
      })
    
      

      // Fetch followers count
      const followersRef = ref(db, `users/${user.uid}/followers`);
      get(followersRef)
        .then((snapshot) => {
          if (snapshot.exists()) {
            const followers = snapshot.val();
            const count = Object.keys(followers).length;
            setFollowersCount(count);
          } else {
            setFollowersCount(0);
          }
        });
  
      // Fetch following count
      const followingRef = ref(db, `users/${user.uid}/following`);
      get(followingRef)
        .then((snapshot) => {
          if (snapshot.exists()) {
            const following = snapshot.val();
            const count = Object.keys(following).length;
            setFollowingCount(count);
          } else {
            setFollowingCount(0);
          }
        });
    }
  }, [user, myUID, targetUserUID]);
  

  const handleFollow =  () => {
    setFollow(!follow);
  
    if (!follow) {

      const notiRef = ref(db, `notification/${targetUserUID}/`)
      // Follow the user
      const updates = {
        [`users/${targetUserUID}/followers/${myUID}`]: true,
        [`users/${myUID}/following/${targetUserUID}`]: true,
      };
  
      update(ref(db), updates)
        .then(() => {
          console.log(`You followed ${targetUserUID}`);
          const noti = push(notiRef,{
            uid: myUID,
            message: "started following you",
            read : false,
            timestamp: Date.now()
          })
          const notiKey = noti.key
          set(ref(db, `notification/${targetUserUID}/${notiKey}`),{
            uid: myUID,
            message: "started following you",
            read : false,
            timestamp: Date.now(),
            key:notiKey
          })


        })
        .catch((error) => {
          console.error('Follow update failed: ', error);
        });
    } else {
      const notiRef = ref(db, `notification/${targetUserUID}/`)
      // Unfollow the user
      const updates = {
        [`users/${targetUserUID}/followers/${myUID}`]: null,
        [`users/${myUID}/following/${targetUserUID}`]: null,
      };
  
      update(ref(db), updates)
        .then(() => {
          console.log(`You unfollowed ${targetUserUID}`);
          const noti = push(notiRef,{
            uid: myUID,
            message: "unfollowed you",
            read : false,
            timestamp: Date.now()
          })
          const notiKey = noti.key
          set(ref(db, `notification/${targetUserUID}/${notiKey}`),{
            uid: myUID,
            message: "unfollowed you",
            read : false,
            timestamp: Date.now(),
            key:notiKey
          })
        })
        .catch((error) => {
          console.error('Unfollow update failed: ', error);
        });
    }

  };
  
  if (!user) {
    return null; // or render a loading indicator or some other component
  }
  const isFollowing = user && user.followers && user.followers[myUID] === true;

  const handleUsernameClick = () => {
    setShowInstantInfo(true);
  };

  const handleFollowerShow = () => {
    if(user){
        window.location.href = `${user?.uid}/followers`
    }else{
      window.location.href = `${myUID}/followers`
    }
  }
  const handleFollowingShow = () => {
    if(user){
        window.location.href = `${user?.uid}/following`
    }else{
      window.location.href = `${myUID}/following`
    }
  }


  return (
    <>
    {showInstantInfo && (
        <UserInstantDetails instant={() => setShowInstantInfo(false)} userId={user?.uid} />
      )}
      <div className='profile-full'>
        <div className="profile-left">
          <img src={profileURL} alt="" className='profile-pic' />
        </div>
        <div className="profile-right">
          <div className="pro-main-info">
            <div className="user-details">
            <h1 className='profilename' onClick={handleUsernameClick}>{user.profilename}</h1>
              <p className='username' onClick={handleUsernameClick}>@{user.username}</p>
            </div>
          </div>
          <div className="profile-main-buttons">
            {myUsername === user.username ?  null : (
              <button className='follow' onClick={handleFollow}>
                {isFollowing ? 'Unfollow' : 'Follow'}
              </button>
            )}
            
          </div>
        </div>
      </div>
      <div className="profile-status" onClick={() => console.log(user.uid)}>
        <div className="pro-status">
          <p>Blogs <br/> {blogsCount}</p>
          <p onClick={handleFollowerShow}>Followers <br/> {followersCount}</p>
          <p onClick={handleFollowingShow}>Following <br/> {followingCount}</p>
        </div>
        <div className="profile-edu">
          <h2>{user.department} '{user.section}' Section <span>({user.year} Year)</span></h2>
          <p>{user.about}</p>
        </div>
        <div className="profile-button-container">
          <button className={selectedButton === "ProfileFolio" ? "select" : "pro-button"} onClick={() => { setSelectedButton("ProfileFolio") }}>
            ProfileFolio
          </button>
          <button className={selectedButton === "Blogs" ? "select" : "pro-button"} onClick={() => { setSelectedButton("Blogs") }}>
            BlogSection
          </button>
          <button className={selectedButton === "ConnectMe" ? "select" : "pro-button"} onClick={() => { setSelectedButton("ConnectMe") }}>
            ConnectMe
          </button>
        </div>
      </div>
      <ProfileBottom selectedButton={selectedButton} user={user} />
    </>
  );
};

export default ProfileTop;
