import React, { useState, useEffect } from 'react';
import '../ComponentsCSS/profilefolio.css';
import AOIBox from './AOIBox';
import SkillBox from './SkillBox';
import { db } from '../firebase';
import { ref } from 'firebase/database';
import CreateProfileFolio from './CreateProfileFolio';

const ProfileFolio = ({ data }) => {



  return (
    <div >
    <h1>
      Welcome to my<span>ProfileFolio</span>
    </h1>
    <div className="area-of-interests" onClick={() => console.log(data)}>
      <h1>Area of Interests:</h1>
      {Object.entries(data.aoi).map(([aoiKey, aoiValue]) => (
        <AOIBox key={aoiKey} data={aoiValue} />
      ))}
    </div>
    <div className="area-of-interests">
      <h1>Skillset Showcase:</h1>
      {Object.entries(data.skills).map(([skillKey, skillValue])=>(
          <SkillBox key={skillKey} data={skillValue}/>
      ))}
    </div>
  </div>
  );
};

export default ProfileFolio;
