import React,{useState} from 'react'
import Header from '../Components/Header'
import NavBar from '../Components/NavBar'
import OnCampusEvent from '../Components/OnCampusEvent'
import OnCampusEvents from '../oncampus_events.json'
import NoEvents from '../Components/NoEvents'
import OffCampusEvent from '../Components/OffCampusEvents'
import OffCampusEvents from '../offcampus_events.json'



const Events = () => {
  const [selectedButton, setSelectedButton] = useState("On-Campus Events")
  return (
    <div className='events-container'>
        <Header/>
        <div className="body-container">
            <NavBar/>
        </div>
        <div className="event-space">
          <div className="event-toggle">
            <button onClick={()=>{setSelectedButton("On-Campus Events")}} className={selectedButton==="On-Campus Events" ? "selected" : ""}>On-Campus Events</button>
            <button onClick={()=>{setSelectedButton("Off-Campus Events")}} className={selectedButton==="Off-Campus Events" ? "selected" : ""}>Off-Campus Events</button>
          </div>
          {selectedButton === "On-Campus Events" && OnCampusEvents.length > 0 ? (OnCampusEvents.map((OnCEvent , index)=> (
            <OnCampusEvent key={index} on_event={OnCEvent}/>
          ))) : OnCampusEvents.length === 0 ? (
            <NoEvents />
          ) : null}
          {selectedButton === "Off-Campus Events" && OffCampusEvents.length > 0 ? (OffCampusEvents.map((OffCEvent , index)=> (
            <OffCampusEvent key={index} off_event={OffCEvent}/>
          ))) : OnCampusEvents.length === 0 ? (
            <NoEvents />
          ) : null}
        </div>
    </div>
  )
}

export default Events