import React,{useState} from 'react'
import RecoBtn from '../Assets/reco-btn.png'
import NoRecoBtn from '../Assets/not-reco.png'
import { ref, set, push, get } from 'firebase/database'
import { db } from '../firebase'
import { useParams } from 'react-router-dom'


const SkillBox = ({data}) => {
  const [reco, setReco] = useState(false)
  const {uid} = useParams()
  const realuid = localStorage.getItem('uid')


  const toggleRecommendation = () => {
    setReco(!reco);
    if (!reco){
      const recoRef = ref(db, `users/${uid}/profilefolio/recos/`)
      push(recoRef,{
        
      })
    }
  }



  return (
    <div className="skill-box" >
        <div className="aoi-info">
        <img src={data.url} alt="" />
        <h1>{data.head}</h1>
        </div>
        <div className="aoi-reco">
        <img src={reco ? RecoBtn : NoRecoBtn} alt="" onClick={toggleRecommendation} disabled={realuid === uid}/>
        <p>198</p>
        </div>
    </div>
  )
}

export default SkillBox