import React from 'react'
import NavBar from '../Components/NavBar'
import Header from '../Components/Header'
import { toast } from 'react-toastify'
import ProEditLogo from '../Assets/pro-edit.png'

const EditChoice = () => {
  return (
    <div>
    <Header/>
    <div className="body-container">
    <NavBar />
    <div className="edit-container">
        <h1>Choose what you want to Edit!</h1>
        <div className="buttons-container">
            <div className='button' onClick={()=>{window.location.href = '/userinfoedit'}}>
              <img src='https://cdn-icons-png.flaticon.com/128/1/1176.png' alt="" className='edit-logo'/>
              User Information
            </div>
            <div className='button' onClick={()=>{window.location.href = '/profile-picture-edit'}}>
              <img src='https://cdn-icons-png.flaticon.com/128/739/739249.png' alt="" className='edit-logo'/>
              Profile Picture
            </div>
            <div className='button' onClick={()=>{window.location.href = '/socialmedia-edit'}}>
              <img src='https://cdn-icons-png.flaticon.com/128/10284/10284188.png' alt="" className='edit-logo'/>
              ConnectMe Section
            </div>
        </div>
        <h1>Log Out:</h1>
        <div className="buttons-container">
          <div className='button' onClick={() =>{localStorage.clear()
              toast.success("Successfully Logged Out")
              window.location.href = '/'}} style={{color:'red'}}>
              <img src='https://cdn-icons-png.flaticon.com/128/4034/4034229.png' alt="" className='edit-logo'/>
               Logout from {localStorage.getItem('username')}
            </div>
        </div>
        
    </div>
       

    </div>
</div>
  )
}

export default EditChoice


