import { get, ref, remove } from 'firebase/database'
import React, { useEffect, useState } from 'react'
import { db } from '../firebase'
import { useParams } from 'react-router-dom'
import { formatDistanceToNow } from 'date-fns';
import { toast } from 'react-toastify'
import Timestamp from './Timestamp'

const Comment2 = ({key, id, com, postkey, post }) => {
    const [user, setUser] = useState()
    const [comment, setComment ] = useState()
    const uid = localStorage.getItem('uid')

    useEffect(()=>{
        const commentRef = ref(db, `posts/${postkey}/comments/${com.key}`)
        get(commentRef)
        .then((snapshot)=>{
            if(snapshot.exists()){
                setComment(snapshot.val())
  
            }

        })
        const userRef = ref(db, `users/${comment?.user}/`)
        get(userRef)
        .then((snapshot)=>{
            if(snapshot.exists()){
                setUser(snapshot.val())
            }
        })
    })

    const handleDelete = () => {
        const commentRef = ref(db, `posts/${postkey}/comments/${com?.key}`);
    const userConfirmation = window.confirm('Are you sure you want to delete this comment?');
    if (userConfirmation) {
      remove(commentRef).then(() => {
        window.location.reload();
        toast.success('comment is deleted successfully!');
      });
    }
    }

    const formattedTimestamp = formatDistanceToNow(new Date(com?.timestamp), { addSuffix: true });


        
  return (
<div className='com-con'>
    <div className='comment-box' onClick={()=>{console.log(id?.user)}}>
        <img src={user?.profiles?.profileURL} alt="" className='comment-profile' onClick={()=>console.log(com)} />
            <div className="user-details-comment">
              <h1>{user?.profilename}</h1>
              <p>{user?.department} "{user?.section}" {user?.year} Year</p>
            </div>
    </div>
    <div className={post?.user === comment?.user ? 'reply' : 'comment-content'}>
    {<p >{comment?.content}</p>}
    {id?.user === uid && <div className="post-function" onClick={handleDelete}>
            <img src="https://cdn-icons-png.flaticon.com/128/6861/6861362.png" alt="" /> 
          </div>}
    </div>
    {com?.timestamp &&  <p className='timestamp' style={{color:'white'}}>- recorded {formattedTimestamp}</p>}


    

    
    </div>
      )
}

export default Comment2