import React, { useState } from 'react'
import "../ComponentsCSS/Loginbox.css"
import { auth, db } from '../firebase'
import { createUserWithEmailAndPassword } from 'firebase/auth'
import { useNavigate } from 'react-router-dom'
import 'react-toastify/dist/ReactToastify.css'
import { toast } from 'react-toastify'
import {push, ref, set} from 'firebase/database'

const SignUpBox = () => {
    const navigate = useNavigate()

    const handleSubmit = (e) => {
        e.preventDefault();
        const email = e.target.email.value;
        const username = email.split('@veltechmultitech.org')[0];
        const password = e.target.password.value;
      
        if (!email.includes('@veltechmultitech.org')) {
          toast.error('Error creating user: Email must be a @veltechmultitech.org address.');
          return;
        }
      
        createUserWithEmailAndPassword(auth, email, password)
          .then((userCredential) => {
            const user = userCredential.user;
            localStorage.setItem('email', user.email);
            localStorage.setItem('uid', user.uid);
            localStorage.setItem('username',username);
            toast.success(`Welcome Home, @${username}`, {
              className: 'custom-toastify-success'
            });
            navigate('/userinfo');
          })
          .catch((error) => {
            const errorCode = error.code;
            const errorMessage = error.message;
            toast.error(`Error creating user: ${errorMessage}, ${errorMessage}`);
          });
      };
  return (
    <form className='login-box'onSubmit={(e) => handleSubmit(e)}>
            <h1>Sign Up:</h1>
            <input name='email' type="text" placeholder='Veltech email id...' className='email' />
            <input name='password' type="password" placeholder='Password...' className='password' />
            <button >Sign Up</button>
    </form>
  )
}

export default SignUpBox