import React, {useState} from 'react'
import securityPic from '../Assets/security.png'

const Socialnote = ({note}) => {


    

    const username = localStorage.getItem("username");
    const offNote = () => {
        note(false)
        window.scrollTo(0, 0);
    }

  return (
    <>
        <div className='social-note-container'>
            <h1>Privacy Protection from ConnectVTMT Team!</h1>
            <img src={securityPic} alt="" />
            <p>We will ensure the topmost privacy will be provide for every one of the Vel Tech Multi Tech Student user and anything that happens to you which make you doubt about our security, we will be available 24/7 online and even you meet us in personal in the college campus at any allotted time! <br/> <span>Again, We will ensure that the User experience of this web app is good. Happy Connecting {username}</span> </p>
            <button onClick={offNote}>Close</button>
        </div>
        </>
  )
}

export default Socialnote