import React, { useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { useState } from 'react'
import "../ComponentsCSS/userinfo.css"
import { toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import { db  } from '../firebase'
import { ref , set, push, get, update } from 'firebase/database'
import Footer from '../Components/Footer'
import Logo from '../Components/Logo'
import Header from '../Components/Header'

const UserInfoEdit = () => {
    const [adminName, setAdminName] = useState('')
    const [adminAbout, setAdminAbout] = useState('')
    const [adminVM, setAdminVM] = useState('')
    const [adminRegNo, setAdminRegNo] = useState('')
    const [selectedDept, setSelectedDept] = useState('')
    const [selectedSection, setSelectedSection] = useState('')
    const [selectedYear, setSelectedYear] = useState('')
    const [errors, setErrors] = useState({})
    const [uploading, setUploading] = useState(false);
    const uid = localStorage.getItem('uid')

    useEffect(()=>{
        const userRef = ref(db,`users/${uid}`);
        get(userRef)
        .then((snapshot)=>{
            if(snapshot.exists()){
                const user = snapshot.val()
                console.log(user)
                setAdminName(user.profilename)
                setAdminAbout(user.about)
                setAdminRegNo(user.reg_no)
                setAdminVM(user.vm_no)
                setSelectedDept(user.department)
                setSelectedSection(user.section)
                setSelectedYear(user.year)
            }
        })
    },[])
  
  
    const handleName = (e) => {
      e.preventDefault();
      setAdminName(e.target.value)
  
    }
    const handleVM = (e) => {
      e.preventDefault();
      setAdminVM(e.target.value)
  
    }
    const handleRegNo = (e) => {
      e.preventDefault();
      setAdminRegNo(e.target.value)
  
    }
    const handleAbout = (e) => {
      e.preventDefault();
      setAdminAbout(e.target.value)
  
    }
  
    const handleChangeDept = (e) => {
      setSelectedDept(e.target.value)
    }
    const handleChangeSection = (e) => {
      setSelectedSection(e.target.value)
    }
    const handleChangeYear = (e) => {
      setSelectedYear(e.target.value)
    }
  
      const navigate = useNavigate()
  
  
      const validate = () => {
        let errors = {};
        if (!adminName){
          errors.adminName = "Name is required"
          toast.error(errors.adminName)
        }
        if(!adminVM){
          errors.adminVM = "VM Number is reqired"
          toast.error(errors.adminVM)
        }
        if(!adminRegNo){
          errors.adminRegNo = "Register Number is reqired"
          toast.error(errors.adminRegNo)
        }
        if(!adminAbout){
          errors.adminAbout = "About yourself is reqired"
          toast.error(errors.adminAbout)
        }
        if(!selectedDept){
          errors.selectedDept = "Department is reqired"
          toast.error(errors.selectedDept)
        }
        if(!selectedSection){
          errors.selectedSection = "Section is reqired"
          toast.error(errors.selectedSection)
        }
        if(!selectedYear){
          errors.selectedYear = "Year you are studying is reqired"
          toast.error(errors.selectedYear)
        }
  
        return errors;
      }
  
      const handleSubmit = () => {
        let errors = validate();
        if(Object.keys(errors).length) return setErrors(errors)
  
          const profileName = adminName
          const vmNo = adminVM
          const regNo = adminRegNo
          const about = adminAbout
          const department = selectedDept
          const section = selectedSection
          const year = selectedYear
          const email = localStorage.getItem('email');
          const id = localStorage.getItem('uid');
          const username = localStorage.getItem('username')
          const profile = {
            email: email,
            username: username,
            uid: id,
            profilename : adminName,
            vm_no: adminVM,
            reg_no : adminRegNo,
            about : adminAbout,
            department: selectedDept,
            section: selectedSection,
            year: selectedYear,
            classroom: `${selectedDept}_${selectedSection}_${selectedYear}`
          }
  
          const userRef = ref(db, `/users/${id}`);
          update(userRef, profile)
  
          navigate('/profile')  
      }
      
  
  
  
  return (
    <>
    <Header/>
      <div className='user-info'>
          <div className='user-detail-form'>
            <h1>User Details:</h1>
            <input type="text" name='name' value={adminName} placeholder='Enter your name...' onChange={handleName} errors = {errors.adminName ? {context: errors.adminName} :  null}/>
            <input type="text" name='vm-no' value={adminVM} placeholder='Enter your VM no (eg:VM150xx)...' onChange={handleVM} errors = {errors.adminVM ? {context: errors.adminVM} :  null}/>
            <input type="text" name='reg-no' value={adminRegNo} placeholder='Enter you Unv. Reg no (eg:113122UG04xxx)...' onChange={handleRegNo} errors = {errors.adminRegNo ? {context: errors.adminRegNo} :  null} />
            <textarea type="text" name='about' value={adminAbout} placeholder='Enter about yourself...' onChange={handleAbout} errors = {errors.adminAbout ? {context: errors.adminAbout} :  null}></textarea>
            

            <select name="dept" value={selectedDept} id="dept" onChange={handleChangeDept} errors = {errors.selectedDept ? {context: errors.selectedDept} :  null}>
              <option value="" disabled={true} >Select your Department</option>
              <option value="Computer Science Engineering" name="cse">Computer Science Engineering</option>
              <option value="Electronics and Communication Engineering" name="ece">Electronics and Communication Engineering</option>
              <option value="Electrical and Electronics Engineering" name="eee">Electrical and Electronics Engineering</option>
              <option value="Artificial Intelligence and Data Science" name="aids">Artificial Intelligence and Data Science</option>
              <option value="Computer Science and Business Systems" name="csbs">Computer Science and Business Systems</option>
              <option value="Mechanical Engineering" name="mech">Mechanical Engineering</option>
              <option value="Information Technology" name="it">Information Technology</option>
            </select>
            <div className="college-info">
              <select name="section" value={selectedSection} id="section" onChange={handleChangeSection} errors = {errors.selectedSection ? {context: errors.selectedSection} :  null}>
                <option value="" disabled>Section</option>
                <option value="A">A</option>
                <option value="B">B</option>
                <option value="C">C</option>
              </select>
              <select name="year" value={selectedYear} id="year" onChange={handleChangeYear} errors = {errors.selectedYear ? {context: errors.selectedYear} :  null}>
                <option value="" disabled>Year</option>
                <option value="1st">1st Year</option>
                <option value="2nd">2nd Year</option>
                <option value="3rd">3rd Year</option>
                <option value="4th">4th Year</option>
              </select>
            <div/>
            
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              </div>


              <button onClick={handleSubmit}>Submit</button>
          </div>
          

      </div>
      <Footer/>
    </>

  )
}

export default UserInfoEdit