import React, {useEffect, useState} from 'react'
import Header from '../Components/Header'
import NavBar from '../Components/NavBar'
import ProfileTop from '../Components/ProfileTop'
import '../ComponentsCSS/profile.css'
import { ref, orderByChild, equalTo, query, onValue, off, get } from 'firebase/database'
import { db, auth } from '../firebase'
import ProfileBottom from '../Components/ProfileBottom'
import ProfileUserTop from '../Components/ProfileUserTop'
import { useParams } from 'react-router-dom'

const UserPage = () => {
    const { uid } = useParams();
    const [userData, setUserData] = useState(null);
    const [loading, setLoading] = useState(true);


    const userRef = ref(db, `users/${uid}/`)

    get(userRef)
    .then((snapshot)=>{
        if(snapshot.exists()){
            setUserData(snapshot.val())
        }else {
            console.log(`No data found for uid: ${uid}`);
          }
    }, [uid])

    .finally(() => {
      setLoading(false); 
    });

   


  return (
    <div className='profile-container'>
        <Header/>
        <div className="body-container">
            <NavBar/>
            <div className="profile-box">
            {loading && (
            <div className="loading-overlay">
              <div className="loading-spinner"></div>
            </div>
          )}
              <ProfileTop user={userData} uid={uid}/>

            </div>
        </div>
    </div>
  )
}

export default UserPage