import React,{useState} from 'react'
import '../ComponentsCSS/eventbox.css'


const OnCampusEvent = ({on_event}) => {
  const [know, setKnow] = useState("Know More")

  const handleKnowClick = () =>{
    setKnow(know === "Know More" ? "Know Less" : "Know More");
  }

  const detailsClassName = know === 'Know Less' ? 'event-details show-details' : 'event-details';

  return (
    <>
    <div className='event-box'>
        <img src={process.env.PUBLIC_URL + on_event.image}  alt="" className='event-image'/>
        <div className="event-info">
            <div className="event-headers">
                <h1>{on_event.name}</h1>
                <p>@{on_event.college}</p>
            </div>
            <button onClick={handleKnowClick}>{know}</button>
        </div>
        {know === "Know Less" ? <div className={detailsClassName}>
            <p>{on_event.desc}</p>
            <button onClick={()=> {
              window.location.href = on_event.reglink;
            }} className='register-btn'>Register</button>
        </div> : ""}
    </div>
    </>
  )
}

export default OnCampusEvent