import './App.css';
import React from 'react';
import { BrowserRouter as Router , Route , Routes, Navigate } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Authpage from './Pages/Authpage';
import UserInfo from './Pages/UserInfo';
import ProfilePic from './Pages/ProfilePic';
import SocialMedia from './Pages/SocialMedia';
import Home from './Pages/Home';
import Search from './Pages/Search';
import Events from './Pages/Events';
import Profile from './Pages/Profile';
import Notfication from './Pages/Notfication';
import UserPage from './Pages/UserPage';
import CreateProfileFolioPage from './Pages/CreateProfileFolioPage';
import SkillPage from './Pages/SkillPage';
import UserInfoEdit from './Pages/UserInfoEdit';
import ProfilePicEdit from './Pages/ProfilePicEdit';
import PostPage from './Pages/PostPage';
import Followers from './Pages/Followers';
import Following from './Pages/Following';
import PostCreateButton from './Components/PostCreateButton';
import ConnectInfoEdit from './Pages/ConnectInfoEdit';
import EditChoice from './Pages/EditChoice';
import WhatsappNoti from './Pages/WhatsappNoti';
import WhatsappHomeSwiper from './Pages/WhatsappHomeSwiper';


function App() {
  const isLoggedIn = !!localStorage.getItem('username'); // Check if the user is logged in

  const AuthRoute = ({ element }) => {
    return isLoggedIn ? <Navigate to="/home" /> : element;
  };
  return (
    <Router>
      <div className='App'>
        <ToastContainer position='top-center'/>
        <Routes>
        <Route
            path="/"
            element={<AuthRoute element={<Authpage />} />}
          />
          <Route  path='/userinfo' Component={UserInfo}></Route>
          <Route  path='/userinfoedit' Component={UserInfoEdit}></Route>
          <Route  path='/profile-picture-upload' Component={ProfilePic}></Route>
          <Route  path='/profile-picture-edit' Component={ProfilePicEdit}></Route>
          <Route  path='/socialmedia' Component={SocialMedia}></Route>
          <Route  path='/socialmedia-edit' Component={ConnectInfoEdit}></Route>
          <Route  path='/home' Component={Home}></Route>
          <Route  path='/search' Component={Search}></Route>
          <Route  path='/events' Component={Events}></Route>
          <Route  path='/profile' Component={Profile}></Route>
          <Route  path='/profile/:uid' Component={UserPage}></Route>
          <Route  path='/search/profile/:uid' Component={UserPage}></Route>
          <Route  path='/notification' Component={Notfication}></Route>
          <Route  path='/createprofilefolio/:userUID' Component={CreateProfileFolioPage}></Route>
          <Route  path='/skillset/:uid' Component={SkillPage}></Route>
          <Route  path='/post/:postkey' Component={PostPage}></Route>
          <Route  path='/:uid/followers' Component={Followers}></Route>
          <Route  path='search/profile/:uid/followers' Component={Followers}></Route>
          <Route  path='/profile/:uid/followers' Component={Followers}></Route>
          <Route  path='/:uid/following' Component={Following}></Route>
          <Route  path='profile/:uid/following' Component={Following}></Route>
          <Route  path='search/profile/:uid/following' Component={Following}></Route>
          <Route  path='/compose' Component={PostCreateButton}></Route>
          <Route  path='/editinfo' Component={EditChoice}></Route>
          <Route  path='/whatsappnoti' Component={WhatsappNoti}></Route>
          <Route Component={WhatsappHomeSwiper}/>
          
        </Routes>
      </div>
    </Router>
  );
}

export default App;
