import React from 'react'
import NoProfileIMG from '../Assets/noprofile.png'

const NoProfileFolio = () => {
  return (
    <div className='no-prof'>
      <h1>No ProfileFolio</h1>
      <p>Please check my <span>ConnectMe</span> to connect with me and build your network with me!</p>
      <img src={NoProfileIMG} alt="" />
    </div>
  )
}

export default NoProfileFolio