import React,{useEffect, useState} from 'react'
import Header from '../Components/Header'
import NavBar from '../Components/NavBar'
import { auth, db } from '../firebase'
import '../ComponentsCSS/search.css'
import SearchBtn from '../Assets/search-btn.png'
import WinkGirlPNG from '../Assets/sexi-woman-pop-art-vector-removebg-preview.png' 
import SearchItem from '../Components/SearchItem'
import { get, ref, onValue } from 'firebase/database'

const Search = () => {
  const [searchValue, setSearchValue] = useState("")
  const [searchItem, setSearchItem ] = useState("notshow");
  const [userUIDs , setUserUIDs ] = useState([]);
  const [loading, setloading ] = useState(true);

  useEffect(() =>{
    const userRef = ref(db, 'users');
    get(userRef)
    .then((snapshot)=>{
      if(snapshot.exists()){
        const users = snapshot.val()
        const usersUIDArray = Object.keys(users);
        console.log(users)
        // console.log(userUIDs)
        setUserUIDs(usersUIDArray);
        setloading(false)
      
      }
    })
  },[])

  const handleSearch = (e) => {
    e.preventDefault();
    setSearchValue(e.target.value)
    if (e.target.value !== "") {
      setSearchItem("show");
    } else {
      setSearchItem("notshow");
    }

  }

  const toggleSearchBar=() =>{
    // setSearchItem(searchItem === "show" ? "notshow" : "show");
  }

  

  return (
    <>
      <Header/>
      <NavBar/>
      <div className="search-container">
        
        <input type="text" placeholder='Search username' className='search-input' onChange={handleSearch} onClick={toggleSearchBar} />
      </div>
      { searchItem==="show" && <div className="search-item-container">
      {loading && (
                <div className="loading-overlay">
                    <div className="loading-spinner"></div>
                </div>
            )}
        {userUIDs.map((uid, index)=>(
          <SearchItem uid={uid} key={index} searchValue={searchValue}/>
        ))
          }
        </div>}
      <div className="search-body">
        <h1>You are curious about someone in the campus, right?!</h1>
        <p>Search for your Collegemates here!</p>
      </div>
      <img src={WinkGirlPNG} alt="" className='wink-girl' />

    </>
  )
}

export default Search
