import React, { useState } from 'react';
import '../ComponentsCSS/navbar.css';
import { useNavigate } from 'react-router-dom';
import { auth } from '../firebase';

const NavBar = ({ user }) => {
  const navigate = useNavigate();

  const [selectedButton, setSelectedButton] = useState('');

  const handleSignOut = async () => {
    try {
      await auth.signOut();
      localStorage.clear();
      navigate('/');
    } catch (error) {
      console.error('Error signing out:', error);
    }
  };

  const handleButtonClick = (button) => {
    navigate(`/${button}`);
    setSelectedButton(button);

  };

  const profile = localStorage.getItem('profile_pic');

  return (
    <div className="navbar-container">
      <div
        className={`nav-button ${selectedButton === 'home' ? 'selected-nav' : ''}`}
        onClick={() => handleButtonClick('home')}
      >
        <img src="https://cdn-icons-png.flaticon.com/512/25/25694.png" alt="" />
        <h2>Home</h2>
      </div>
      <div
        className={`nav-button ${selectedButton === 'search' ? 'selected-nav' : ''}`}
        onClick={() => handleButtonClick('search')}
      >
        <img src="https://cdn-icons-png.flaticon.com/512/61/61088.png" alt="" />
        <h2>Search</h2>
      </div>
      <div
        className={`nav-button ${selectedButton === 'events' ? 'selected-nav' : ''}`}
        onClick={() => handleButtonClick('events')}
      >
        <img src="https://www.citypng.com/public/uploads/small/11640487321kzngzwhl4xjx4fmyj7fpzntijpwi9ubqobum84ysu6zolaibglvy9o07wqaordtxe6cdarnhrofibhfxrvzwwmfkedcylp7x0sn2.png" alt="" />
        <h2>Events</h2>
      </div>
      <div
        className={`nav-button ${selectedButton === 'notification' ? 'selected-nav' : ''}`}
        onClick={() => handleButtonClick('notification')}
      >
        <img src="https://cdn-icons-png.flaticon.com/512/565/565422.png" alt=""  />
        <h2>Notification</h2>
      </div>
      <div
        className={`nav-profile ${selectedButton === 'profile' ? 'selected-nav' : ''}`}
        onClick={() => handleButtonClick('profile')}
      >
        <img src={profile} alt="" className="profile-nav" />
        <h2>Profile</h2>
      </div>

      <div className="nav-button signout" onClick={handleSignOut}>
        <img src="https://cdn-icons-png.flaticon.com/512/25/25376.png" alt="" />
        <h2>Signout</h2>
      </div>
    </div>
  );
};

export default NavBar;
