import React, { useEffect, useRef, useState } from 'react';
import { db } from '../firebase';
import { ref, get, update, remove, onValue, push } from 'firebase/database';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import likeSound from '../Components/like.mp3'; 
import NikhilPNG from '../Assets/nikhil-post-template.jpg'
import CommentBox from './CommentBox';
import InstantImg from '../Assets/instant-pic.png'
import Timestamp from './Timestamp';
import { set } from 'date-fns';


const Post = ({ postKey, post, id, instant, handleCommentBox, commentBox }) => {
  const [doubleTap, setDoubleTap] = useState(false);
  const [likedUser, setLikedUser] = useState([]);
  const [user, setUser] = useState();
  const [likeCount, setLikeCount] = useState(0);
  const [commentPost, setCommentPost] = useState('');
  const [lengthComments, setLengthComment] = useState(0)
  const [comments , setComments ] = useState([]);
  const [uid, setUid] = useState('');
  const audioRef =useRef(null)

  useEffect(() => {
    const userInfo = ref(db, `users/${id}`);
    get(userInfo).then((snapshot) => {
      if (snapshot.exists()) {
        setUser(snapshot.val());
      }
    });

    const postLikesRef = ref(db, `posts/${post?.postKey}/likes`);
    get(postLikesRef).then((snapshot) => {
      if (snapshot.exists()) {
        const likes = snapshot.val();
        setLikedUser(Object.keys(likes));
        setLikeCount(Object.keys(likes).length);
      }
    });

    setUid(localStorage.getItem('uid'));
  }, [id, post?.postKey]);

useEffect(() => {
     const commentRef = ref(db, `posts/${post?.postKey}/comments`);
      get(commentRef)
      .then((snapshot)=>{
        if(snapshot.exists()){
          const commentList = snapshot.val();
          const reversedComments = commentList ? Object.values(commentList).reverse() : [];
          setComments(reversedComments);
          
          const lengthOfComments = reversedComments.length;
          setLengthComment(lengthOfComments)
          console.log(lengthOfComments);
        } else {
          setComments([]);
        }
      })
  }, [post?.postKey]);



  const navigate = useNavigate();


  const handleLike = () => {
    const notiRef = ref(db, `notification/${post?.user}/`)
    const updates = {};
    if (likedUser.includes(uid)) {
      updates[`posts/${post?.postKey}/likes/${uid}`] = null;
    } else {
      updates[`posts/${post?.postKey}/likes/${uid}`] = true;
    }

    update(ref(db), updates)
      .then(() => {
        const updatedLikedUser = likedUser.includes(uid)
          ? likedUser.filter((userId) => userId !== uid)
          : [...likedUser, uid];
          if (!likedUser.includes(uid)){
            const noti =push(notiRef,{
            uid: uid,
            message: "liked your post",
            read : false,
            post: post?.postKey,
            timestamp: Date.now()
          })
        const notiKey = noti.key
        set(ref(db, `notification/${post?.user}/`),{
          uid: uid,
            message: "liked your post",
            read : false,
            post: post?.postKey,
            timestamp: Date.now(),
            key : notiKey
        })

        }
        setLikedUser(updatedLikedUser);
        setLikeCount(updatedLikedUser.length);
      })
      .catch((error) => {
        console.error('Error updating like: ', error);
      });
      audioRef.current.play();
  };

  

  const handleDoubleTap = () => {
    if (doubleTap) {

      setDoubleTap(false);
      handleLike(); 
    } else {
      setDoubleTap(true);
      setTimeout(() => {
        setDoubleTap(false);
      }, 300);
    }
  };

  const handleDelete = () => {
    const postRef = ref(db, `posts/${post?.postKey}`)
    const userPost = ref(db, `users/${uid}/posts/${post?.postKey}`)
    const userConfirmation = window.confirm("Are you sure you want to delete this post?")
    if (userConfirmation){
      remove(postRef)
      remove(userPost)
      .then(() => {
        window.location.reload()
        toast.success("Post is deleted successfully!")
      })
    }
    

  }

  const formattedContent = post?.content ? post.content.replace(/\n/g, '<br>') : '';


  return (
    <div className="post-box" onClick={handleDoubleTap}>
      <audio ref={audioRef} src={likeSound} />
      <div className="post-user">
        <img src={user?.profiles?.profileURL} alt="" className="post-user-profile" />
        <div className="user-details-post"  onClick={() => { navigate(`/profile/${user.uid}`) }}>
          <h1 >{user?.profilename}</h1>
          <p>@{user?.username}</p>
        </div>

        <img src={InstantImg} alt="" className="instant-image" onClick={instant}/>
      </div>
      <div className="post-content" dangerouslySetInnerHTML={{ __html: formattedContent }}  />

      {post?.media && (
    <img src={ post.media } alt="" className="post-media" />
  )}

      {post?.mediaType === 'video' && post?.media && (
        <video controls className="post-media" >
          <source src={post.media} type="video/mp4" />
          Your browser does not support the video tag.
        </video>
      )}



      <div className="post-status">
        <div className="post-function">
          <img
            src={
              likedUser.includes(uid)
                ? 'https://icones.pro/wp-content/uploads/2021/02/icone-de-coeur-bleu-1.png'
                : 'https://cdn-icons-png.flaticon.com/128/1077/1077035.png'
            }
            alt=""
            onClick={handleLike}
          />
          <p>{likeCount}</p>
        </div>
          <div className="post-function" >
            <img src="https://cdn-icons-png.flaticon.com/128/2462/2462719.png" alt="" onClick={()=> window.location.href = (`post/${post?.postKey}`)} /> 
            <p>{lengthComments}</p>
          </div>
          <div className="post-function" onClick={instant}>
            <img src="https://cdn-icons-png.flaticon.com/128/2958/2958783.png" alt="" /> 
          </div>
          {id === uid && <div className="post-function" onClick={handleDelete}>
            <img src="https://cdn-icons-png.flaticon.com/128/6861/6861362.png" alt="" /> 
          </div>}
          
        </div>


        <Timestamp timestamp={post?.timestamp} />         
    </div>
  )
}

export default Post