import { get, ref } from 'firebase/database'
import React, { useEffect, useState } from 'react'
import { db } from '../firebase'
import { useParams } from 'react-router-dom'

const Comment = ({key, id, com, post}) => {
    const [user, setUser] = useState()
    const [comment, setComment ] = useState()

    useEffect(()=>{
        const commentRef = ref(db, `posts/${post?.postKey}/comments/${com}`)
        get(commentRef)
        .then((snapshot)=>{
            if(snapshot.exists()){
                setComment(snapshot.val())
  
            }

        })
        const userRef = ref(db, `users/${comment?.user}/`)
        get(userRef)
        .then((snapshot)=>{
            if(snapshot.exists()){
                setUser(snapshot.val())
            }
        })
    })



        
  return (
<div className='com-con'>
    <div className='comment-box'>
        <img src={localStorage.getItem('profile_pic')} alt="" className='comment-profile' onClick={()=>console.log(post)} />
            <div className="user-details-comment">
              <h1>{user?.profilename}</h1>
              <p>{user?.department} "{user?.section}" {user?.year} Year</p>
            </div>
    </div>
    <p className='comment-content'>{comment?.content}</p>

    
    </div>
      )
}

export default Comment