import React, { useEffect, useState } from 'react'
import AddComment from './AddComment'
import Comment from './Comment'
import { get, push, ref, set , onValue} from 'firebase/database';
import { db } from '../firebase';
import { toast } from 'react-toastify';

const CommentBox = ({post, commentBox}) => {
    const [comments , setComments ] = useState([]);
    const [comment, setComment ] = useState('')
  
    useEffect(() => {
      const commentRef = ref(db, `posts/${post?.postKey}/comments`);
  
      const unsubscribe = onValue(commentRef, (snapshot) => {
        if (snapshot.exists()) {
          const commentList = snapshot.val();
          setComments(commentList);
          console.log(comments);
        }
      });
  
return () => unsubscribe();
    }, [post?.postKey]);
  

    const handleCommentInput = (e) => {
      e.preventDefault();
      setComment(e.target.value)

    
}
  
    const handleCommentUpload = () => {
      const commentRef = ref(db, `posts/${post?.postKey}/comments/`);

      const uid = localStorage.getItem('uid');
  
      const newCommentRef = push(commentRef, {
        content : comment,
        user : uid,
      })

      
  
      const commentKey = newCommentRef.key
  
      set(ref(db,`posts/${post?.postKey}/comments/${commentKey}/`), {
        content : comment,
        user : uid,
        key : commentKey
    })
    toast.success("Comment uploaded")
    
    window.location.reload()
    

}
    return (
      <>
<div className="loading-overlay">
    <div className='comment-container'>
        <AddComment handleCommentInput={handleCommentInput} handleCommentUpload={handleCommentUpload} commentBox={commentBox}/>
        {Object.keys(comments).map((com) =>(<Comment com={com} id={com} post={post}/>)) }

      </div>
    </div>
    
    </>
  
  )
}

  export default CommentBox