import React, {useState} from 'react'
import '../ComponentsCSS/profilepic.css'
import '../ComponentsCSS/errors.css'
import Footer from '../Components/Footer'
import Header from '../Components/Header'
import { storage, db } from '../firebase'
import { useNavigate } from 'react-router-dom'
import { getDownloadURL, uploadBytes , ref } from 'firebase/storage'
import { push, set, ref as sRef } from 'firebase/database'
import { toast } from 'react-toastify'
import { get } from 'firebase/database'

const ProfilePic = () => {
    
    const [imageURL , setImageURL ] = useState('https://cdn-icons-png.flaticon.com/512/3177/3177440.png');
    const [image, setImage] = useState(null)
    const [uploading, setUploading] = useState(false);
    const [validate, setValidate] = useState(false)

    const navigate = useNavigate()

    const handleImage = (e) => {
        const uploadedImage = e.target.files[0];
        setImage(uploadedImage);
        setValidate(false)
        if(image){
            
        }


    }

    const handleUpload = () => {
       const id = localStorage.getItem('uid');
       

       setUploading(true);
       
        if (image) {
            setValidate(false)
            const storageRef = ref(storage, `profiles/${id}`);
            
            
            
            uploadBytes(storageRef, image).then(() => {
                getDownloadURL(storageRef).then((downloadURL) => {
                    setImageURL(downloadURL);
                    
                    const profileURL = downloadURL;
                    localStorage.setItem("profile_pic", profileURL)



                    
                    // const profileDbRef = ref(db, `users/${id}/`)

                    // push(profileDbRef, {
                    //     profile_pic : downloadURL
                    // });
                    
                    setUploading(false);

                    


                }).catch((error) => {
                   
                    console.error('Error getting download URL:', error);
                });
            }).catch((error) => {
                // Handle error uploading the image.
                console.error('Error uploading image:', error);
            });
        }else{
            localStorage.setItem('profile_pic', imageURL)
        }
    }



    const handleSubmit = () => {
        if (!image) {
            setValidate(true)
        }else{
            const profile = localStorage.getItem("profile_pic");
        const id = localStorage.getItem('uid');
        const profileRef = sRef(db, `users/${id}/profiles`)

        set(profileRef, {
            profileURL : profile
        })

        navigate("/socialmedia")
        }
    }

  return (
    <>
    <Header/>
    <h1 className='profile-upload-h1'>UPLOAD YOUR PROFILE PICTURE</h1>
        <div className='profile-upload-container'>
        {uploading && (
                <div className="loading-overlay">
                    <div className="loading-spinner"></div>
                </div>
            )}
            
            <img src={imageURL} alt="" className='image-profile'/>
            

            <div className="profile-button">
            <label class={image ? 'label-uploaded' : 'label'}>
                <input type="file" accept='image/*' onChange={handleImage} required/>
                <span>{image ? 'Image uploaded!' : 'Select Image'}</span>
            </label>
                <button onClick={handleUpload}>View</button>
                <button onClick={handleSubmit}>Submit</button>

                
            </div>
            <a onClick={()=> navigate("/socialmedia")} >Skip</a>
           {validate && (<p className='error'>*Adding Profile picture to your account can help others recognize you</p>)}
        
        </div>
        <Footer/>
    </>
  )
}

export default ProfilePic