import React, { useEffect, useState } from 'react'
import InstaPNG from '../Assets/instagram.png'
import LinkedinPNG from '../Assets/linkedin.png'
import GithubPNG from '../Assets/github.png'
import TwitterPNG from '../Assets/twitter.png'
import SpotifyPNG from '../Assets/spotify.png'
import FacebookPNG from '../Assets/facebook.png'
import WhatsappPNG from '../Assets/whatsapp.png'
import '../ComponentsCSS/connectme.css'
import { get, push, ref, set } from 'firebase/database'
import { db } from '../firebase'
import { toast } from 'react-toastify'

const ConnectMe = ({user}) => {
    const uid = localStorage.getItem('uid')
    const username = localStorage.getItem('username')

    useEffect(()=>{
        
    })

    const handleWhatsappRequest = () => {
        const WhatsappPermission = ref(db,`whatsapp-permission/${user?.uid}/${uid}`)
        
        get(WhatsappPermission)
        .then((snapshot)=>{
            if(snapshot.exists()){
                const whatsapp = snapshot.val()
                if(whatsapp?.acception === false){
                    const notiRef = ref(db,`notification/${user?.uid}/`);
                    const whatsappRef = ref(db, `whatsapp-permission/${user?.uid}/${uid}`)
            
                    const noti= push(notiRef,{
                        uid: uid,
                        message: `${username} has requested you for Whatsapp Permission.`,
                        whatsapp: true,
                        read : false,
                        timestamp: Date.now()
            
                    })
                    const notiKey = noti.key
        
                    set(ref(db,`notification/${user?.uid}/${notiKey}`),{
                        uid: uid,
                        message: `${username} has requested you for Whatsapp Permission.`,
                        whatsapp: true,
                        read : false,
                        timestamp: Date.now(),
                        key : notiKey
                    })

                    set(ref(db,`whatsapp-noti/${user?.uid}/${notiKey}`),{
                        uid: uid,
                        message: `${username} has requested you for Whatsapp Permission.`,
                        whatsapp: true,
                        read : false,
                        timestamp: Date.now(),
                        key : notiKey
                    })
            
                    set(whatsappRef,{
                        acception : false
                    })
            
                    toast.success(`Request sent to ${user?.profilename}. Will update you when we get the reply! `)
                
                }else if(whatsapp?.acception===true){
                    window.location.href = `https://api.whatsapp.com/send/?phone=%2B91${user?.socialmedias?.whatsapp}&text&type=phone_number&app_absent=0`
                }
            }else{
                const notiRef = ref(db,`notification/${user?.uid}/`);
                const whatsappRef = ref(db, `whatsapp-permission/${user?.uid}/${uid}`)
        
                const noti= push(notiRef,{
                    uid: uid,
                    message: `${username} has requested you for Whatsapp Permission.`,
                    whatsapp: true,
                    read : false,
                    timestamp: Date.now()
        
                })
                const notiKey = noti.key
    
                set(ref(db,`notification/${user?.uid}/${notiKey}`),{
                    uid: uid,
                    message: `${username} has requested you for Whatsapp Permission.`,
                    whatsapp: true,
                    read : false,
                    timestamp: Date.now(),
                    key : notiKey
                })
                set(ref(db,`whatsapp-noti/${user?.uid}/${notiKey}`),{
                    uid: uid,
                    message: `${username} has requested you for Whatsapp Permission.`,
                    whatsapp: true,
                    read : false,
                    timestamp: Date.now(),
                    key : notiKey
                })
        
                set(whatsappRef,{
                    acception : false
                })
        
                toast.success(`Request sent to ${user?.profilename}. Will update you when we get the reply! `)
            }
        })

        
    }



  return (
    <div className='connect-me-container'>
        <h1 className='connect-h1'>Connect with Me!</h1>
        <div className="social-container">
          {user?.socialmedias?.insta && <div className="instagram-container">
                <img src={InstaPNG} className='social-logo'/>
                <div className="social-button-01">
                    <h1>{user.socialmedias.insta}</h1>
                    <button onClick={() => {window.location.href=`https://www.instagram.com/${user.socialmedias.insta}`}}>Follow Me</button>
                </div>
            </div>}
          {user.socialmedias?.linkedin && <div className="linkedin-container">
                <img src={LinkedinPNG} className='social-logo'/>
                <div className="social-button-01">
                    <h1>{user.socialmedias.linkedin}</h1>
                    <button onClick={()=>{window.location.href=`https://www.linkedin.com/in/${user.socialmedias.linkedin}/`}}>Connect Me</button>
                </div>
            </div>}
          {user.socialmedias?.github && <div className="github-container">
                <img src={GithubPNG} className='social-logo'/>
                <div className="social-button-02">
                    <h1>{user.socialmedias.github}</h1>
                    <button onClick={() => {window.location.href=`https://www.github.com/${user.socialmedias.gitub}`}}>Connect Me</button>
                </div>
            </div>}
          {user.socialmedias?.twitter && <div className="twitter-container">
                <img src={TwitterPNG} className='social-logo'/>
                <div className="social-button-02">
                    <h1>{user.socialmedias.twitter}</h1>
                    <button onClick={() => {window.location.href=`https://www.twitter.com/${user.socialmedias.twitter}`}}>Tweet Me</button>
                </div>
            </div>}
          {user.socialmedias?.facebook && <div className="facebook-container">
                <img src={FacebookPNG} className='social-logo'/>
                <div className="social-button-01">
                    <h1>{user.socialmedias.facebook}</h1>
                    <button>Follow Me</button>
                </div>
            </div>}
          {user.socialmedias?.whatsapp && <div className="whatsapp-container">
                <img src={WhatsappPNG} className='social-logo'/>
                <div className="social-button-03">
                    <h1>{user.profilename}</h1>
                    <button onClick={handleWhatsappRequest}>Chat Now</button>
                </div>
            </div>}
        </div>
        
    </div>
  )
}

export default ConnectMe