import React, {useState} from 'react'
import '../ComponentsCSS/socialmedia.css'
import Header from '../Components/Header'
import Footer from '../Components/Footer'
import { db } from '../firebase'
import { ref , set } from 'firebase/database'
import Socialnote from '../Components/Socialnote'
import { toast } from 'react-toastify'
import { useNavigate } from 'react-router-dom'

const SocialMedia = () => {
    const initialSocialValues = {
        Instagram: '',
        LinkedIn: '',
        WhatsApp: '',
        Github: '',
        Facebook: '',
        Twitter: '',
        Spotify: '',
    };

    const navigate = useNavigate()

   
    const [socialValues, setSocialValues] = useState(initialSocialValues);
    const [note, showNote] = useState(true)

    const socials = [
        {
            app : "Instagram",
            logo : "https://upload.wikimedia.org/wikipedia/commons/thumb/e/e7/Instagram_logo_2016.svg/2048px-Instagram_logo_2016.svg.png",
            placeholder : "Enter Instagram Username"
        },
        {
            app : "LinkedIn",
            logo : "https://upload.wikimedia.org/wikipedia/commons/thumb/c/ca/LinkedIn_logo_initials.png/600px-LinkedIn_logo_initials.png?20140125013055",
            placeholder : "Enter LinkedIn Username"
        },
        {
            app : "WhatsApp",
            logo : "https://www.cdnlogo.com/logos/w/35/whatsapp-icon.svg",
            placeholder : "Enter WhatsApp No."
        },
        {
            app : "Github",
            logo : "https://cdn.pixabay.com/photo/2022/01/30/13/33/github-6980894_1280.png",
            placeholder : "Enter Github Username"
        },
        {
            app : "Facebook",
            logo : "https://pngimg.com/d/facebook_logos_PNG19757.png",
            placeholder : "Enter Facebook URL"
        },
        {
            app : "Twitter",
            logo : "https://freelogopng.com/images/all_img/1690643591twitter-x-logo-png.png",
            placeholder : "Enter Twitter Username"
        },
        {
            app : "Spotify",
            logo : "https://www.freeiconspng.com/uploads/spotify-icon-2.png",
            placeholder : "Enter Spotify Username"
        }
    ]

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setSocialValues((prevValues) => ({
            ...prevValues,
            [name]: value,
        }));
    };

    const handleNote = () => {
        showNote(true)
        window.scrollTo(0, 0);
    }

    const handleSubmit = () => {
        const id = localStorage.getItem('uid');
        const socialMediaRef = ref(db, `users/${id}/socialmedias`);
        
        // Create an object to hold the values you want to set
        const socialMediaValues = {};
      
        
        if (socialValues.Instagram) {
          socialMediaValues.insta = socialValues.Instagram;
        }
      
        if (socialValues.LinkedIn) {
          socialMediaValues.linkedin = socialValues.LinkedIn;
        }
      
        if (socialValues.WhatsApp) {
          socialMediaValues.whatsapp = socialValues.WhatsApp;
        }
      
        if (socialValues.Github) {
          socialMediaValues.github = socialValues.Github;
        }
      
        if (socialValues.Facebook) {
          socialMediaValues.facebook = socialValues.Facebook;
        }
      
        if (socialValues.Twitter) {
          socialMediaValues.twitter = socialValues.Twitter;
        }
      
        if (socialValues.Spotify) {
          socialMediaValues.spotify = socialValues.Spotify;
        }
      
        
        if (Object.keys(socialMediaValues).length > 0) {
          set(socialMediaRef, socialMediaValues);
          toast.success("Thanks for sharing your Social Media informartion! Welcome to ConnectVTMT")
        }

        navigate('/profile')
        

        
      };
      

  return (
    <>
    <Header/>
    {note&& <Socialnote note={showNote} />}
    <h1 className='social-h1'>YOUR SOCIAL INTERACTING PLATFORMS:</h1>
        <div className='social-input-container'>
        {socials.map((social, index) => (
            <div className="social-input" key={index}>
                <img src={social.logo} alt="" className='socialmedia-logo' />
                <input type="text" name={social.app} value={socialValues[social.app]} placeholder={social.placeholder} onChange={handleInputChange}/>
            </div>
        ))}
        <div className="buttons">
            <button onClick={handleNote} >Note</button>
            <button onClick={handleSubmit}>Submit</button>
        </div>
        </div>

    <Footer/>
    </>
  )
}

export default SocialMedia