import { get, ref } from 'firebase/database';
import React, { useEffect, useState } from 'react'
import { db } from '../firebase';
import Header from '../Components/Header';
import NavBar from '../Components/NavBar';
import NotiBox from '../Components/NotiBox';

const WhatsappNoti = () => {
    const uid = localStorage.getItem('uid')
    const [notifications , setNotifications] = useState([])
  
    useEffect(() => {
      const notiRef = ref(db, `whatsapp-noti/${uid}/`);
    
      get(notiRef)
        .then((snapshot) => {
          if (snapshot.exists()) {
            const notificationList = snapshot.val();
            const reversedNotifications = notificationList
              ? Object.values(notificationList).reverse()
              : [];
            setNotifications(reversedNotifications);
          }
        });
    }, [uid]);


  return (
    <div className='notification-container' >
        <Header/>
        <div className="body-container">
            <NavBar/>
            <div className="notification-place" >
            <p className='whatsapp-para'>WhatsApp Notifications</p>

              {notifications? Object.values(notifications)
                .filter(notification => !notification.read)
                .map(notification => (
                  <NotiBox key={notification.notificationId} noti={notification}/>
                )) : "No Notification for now" }            
            </div>
        </div>
    </div>
  )
}

export default WhatsappNoti