import React, { useEffect, useState, useRef } from 'react';
import Header from '../Components/Header';
import NavBar from '../Components/NavBar';
import Post from '../Components/Post';
import '../ComponentsCSS/post.css';
import ComposeIcon from '../Assets/compose.png'
import PostCreateButton from '../Components/PostCreateButton';
import { db, storage } from '../firebase';
import { getDownloadURL, uploadBytes, ref as sRef } from 'firebase/storage';
import { ref, push, set, get } from 'firebase/database';
import { v4 as uuidv4 } from 'uuid';
import { toast } from 'react-toastify';
import UserInstantDetails from '../Components/UserInstantDetails';
import CommentBox from '../Components/CommentBox';

const Home = () => {
  const [posts, setPosts] = useState({});
  const [file, setFile] = useState('');
  const [blogContent, setBlogContent] = useState('');
  const [currentUser, setCurrentUser] = useState([])
  const [mediaURL, setMediaURL] = useState('');
  const [loading, setLoading] = useState(true);
  const [users, setUsers] = useState([]);
  const [instantInfo, showInstantInfo] = useState(false);
  const [commentBox, showCommentBox ] = useState(false)
  const [selectedUserId, setSelectedUserId] = useState();
  const randomUid = uuidv4();
  const [mediaType, setMediaType] = useState('')
  const [comment, setComment ] = useState('')
  const [unreadNoti , setUnreadNoti ] = useState([])
  const [validate, setValidate] = useState(false);
  const [commentBoxPostID, setCommentBoxPostID] = useState(null);
  const imageFileExtensions = ['.jpg', '.jpeg', '.png', '.gif', '.bmp', '.tiff', '.svg', '.webp', '.heic'];
  const videoFileExtensions = ['.mp4', '.avi', '.mkv', '.mov', '.wmv', '.flv', '.webm', '.3gp', '.mpeg', '.ogg'];
  const profilename = localStorage.getItem('profilename')

  const uid = localStorage.getItem('uid');

  useEffect(() => {
    setLoading(true);
    const postRef = ref(db, '/posts');

    get(postRef).then((snapshot) => {
      if (snapshot.exists()) {
        const posts = snapshot.val();
        const postsArray = Object.values(posts);
        const reversedPosts = postsArray.reverse();

        setPosts(reversedPosts);
        setLoading(false);

      }
    });

  }, []);

  

  useEffect(() => {
    const userRef = ref(db, 'users');
    get(userRef).then((snapshot) => {
      if (snapshot.exists()) {
        const users = snapshot.val();
        console.log(users);
        setUsers(users);
      }
    });

  }, []);

  const commentBoxRef = useRef(null);

  const handleClickOutside = (event) => {
    if (commentBoxRef.current && !commentBoxRef.current.contains(event.target)) {
      setCommentBoxPostID(null);
    }
  };

  useEffect(()=>{
    const currentUser = ref(db, `users/${uid}`)
    get(currentUser)
    .then((snapshot)=>{
      if(snapshot.exists()){
        setCurrentUser(snapshot.val())
      }
    })
  })

  useEffect(() => {
    document.addEventListener('mousedown', handleClickOutside);

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [commentBoxRef]);

  const handleBlog = (e) => {
    setBlogContent(e.target.value);
  };

  const handleImage = (e) => {
    const file = e.target.files[0];
    setFile(file);
  
    const imageFileExtensions = ['.jpg', '.jpeg', '.png', '.gif', '.bmp', '.tiff', '.svg', '.webp', '.heic'];
    const videoFileExtensions = ['.mp4', '.avi', '.mkv', '.mov', '.wmv', '.flv', '.webm', '.3gp', '.mpeg', '.ogg'];
  
    const fileExtension = file.name.split('.').pop().toLowerCase();
  
    const isImage = imageFileExtensions.includes(`.${fileExtension}`);
    const isVideo = videoFileExtensions.includes(`.${fileExtension}`);
  
    const mediaType = isImage ? 'image' : isVideo ? 'video' : 'unknown';
    setMediaType(mediaType)
  
    if (file) {
      setLoading(true);
      const id = localStorage.getItem('uid');
      const storageRef = sRef(storage, `images/${id}/${file.name}`);
  
      uploadBytes(storageRef, file)
        .then(() => getDownloadURL(storageRef))
        .then((downloadURL) => {
          setMediaURL(downloadURL);
          console.log('Media uploaded and URL set:', downloadURL);
          setLoading(false);

          console.log('File:', file);
    console.log('Media Type:', mediaType);
        })
        .catch((error) => {
          console.error('Error uploading media:', error);
        });


    } else {
      console.log('No media selected.');
    }
  };
  
  const handleCommentBox = (postID) => {
        setCommentBoxPostID(postID);
        showCommentBox(true)
      };

const handlePostDB = () => {
  if (blogContent === '') {
    toast.error('Blog cannot be empty');
  } else {
    const formattedBlog = blogContent.replace(/\n/g, '<br>');
    const postRef = ref(db, '/posts/');

    const newPostRef = push(postRef, {
      content: formattedBlog,
      user: uid,
      timestamp: Date.now(),
      media: mediaURL,
      mediaType: mediaType, 
    });

    const newPostKey = newPostRef.key;

    set(ref(db, `/posts/${newPostKey}`), {
      content: blogContent,
      user: uid,
      timestamp: Date.now(),
      postKey: newPostKey,
      media: mediaURL,
      mediaType: mediaType, 
    });

    toast.success('Blog posted Successfully, Please Refresh for changes');
  }

  window.location.reload();
};

      
  const handleInstantInfo = (userId) => {
    setSelectedUserId(userId);
    console.log(selectedUserId);
    showInstantInfo(true);
  };

  return (
    <div className="home-page">
      {loading && (
        <div className="loading-overlay">
          <div className="loading-spinner"></div>
        </div>
      )}
      <Header />
      <div className="body-container">
        <NavBar />

        <div className="post-container">
          {commentBox &&  (
            <CommentBox key={commentBoxPostID} post={posts[commentBoxPostID]} id={posts[commentBoxPostID].user} commentBox={showCommentBox}/>
          )}
          {instantInfo && selectedUserId && <UserInstantDetails instant={showInstantInfo} userId={selectedUserId} />}

<img src={ComposeIcon} alt="" className='compose-image' onClick={()=> {window.location.href='/compose'}} />
            
          <h1 className='welcome-home'>Welcome <span>{currentUser?.profilename},</span></h1>
          <p className='welcome-para'>Check out the campus stories!</p>
          {Object.keys(posts).map((postID) => (
            <Post
              key={postID}
              post={posts[postID]}
              id={posts[postID].user}
              instant={() => handleInstantInfo(posts[postID].user)}
              handleCommentBox={() => handleCommentBox(postID)}
              
            />
          ))}
        </div>
      </div>
    </div>
  );
};

export default Home;
