import React, { useState } from 'react'
import { db } from '../firebase';
import { ref , set , push, update  } from 'firebase/database';
import { ref as sRef , uploadBytes, getDownloadURL} from 'firebase/storage';
import { storage } from '../firebase';
import { v4 as uuidv4 } from 'uuid';
import { toast } from 'react-toastify';
import { useNavigate } from 'react-router-dom';
import Header from './Header';
import Footer from './Footer';

const PostCreateButton = ({   handleMediaUpload }) => {
  const [likePost , setLikePost] = useState(false)
  const [file, setFile] = useState('');
  const [blogContent, setBlogContent] = useState('');
  const [commentPost , setCommentPost ] = useState("")
  const randomUid = uuidv4();
  const [mediaType, setMediaType] = useState('')
  const [loading, setLoading] = useState(false);
  const [mediaURL, setMediaURL] = useState('');


  const uid = localStorage.getItem('uid')
  const navigate = useNavigate();

  const handleBlog = (e) => {
    setBlogContent(e.target.value);
  };

  const handleImage = (e) => {
    const file = e.target.files[0];
    setFile(file);
  
    const imageFileExtensions = ['.jpg', '.jpeg', '.png', '.gif', '.bmp', '.tiff', '.svg', '.webp', '.heic'];
    const videoFileExtensions = ['.mp4', '.avi', '.mkv', '.mov', '.wmv', '.flv', '.webm', '.3gp', '.mpeg', '.ogg'];
  
    const fileExtension = file.name.split('.').pop().toLowerCase();
  
    const isImage = imageFileExtensions.includes(`.${fileExtension}`);
    const isVideo = videoFileExtensions.includes(`.${fileExtension}`);
  
    const mediaType = isImage ? 'image' : isVideo ? 'video' : 'unknown';
    setMediaType(mediaType)
  
    if (file) {
      setLoading(true);
      const id = localStorage.getItem('uid');
      const storageRef = sRef(storage, `images/${id}/${file.name}`);
  
      uploadBytes(storageRef, file)
        .then(() => getDownloadURL(storageRef))
        .then((downloadURL) => {
          setMediaURL(downloadURL);
          console.log('Media uploaded and URL set:', downloadURL);
          setLoading(false);

          console.log('File:', file);
    console.log('Media Type:', mediaType);
        })
        .catch((error) => {
          console.error('Error uploading media:', error);
        });


    } else {
      console.log('No media selected.');
    }
  };




  const handlePostDB = () => {
    if (blogContent === '') {
      toast.error('Blog cannot be empty');
    } else {
      const formattedBlog = blogContent.replace(/\n/g, '<br>');
      const postRef = ref(db, '/posts/');
      const userPostref = ref(db, `users/${uid}/posts`)
  
      const newPostRef = push(postRef, {
        content: formattedBlog,
        user: uid,
        timestamp: Date.now(),
        media: mediaURL,
        mediaType: mediaType, 
      });
  
      const newPostKey = newPostRef.key;
  
      set(ref(db, `/posts/${newPostKey}`), {
        content: blogContent,
        user: uid,
        timestamp: Date.now(),
        postKey: newPostKey,
        media: mediaURL,
        mediaType: mediaType, 
      });

      set(ref(db, `users/${uid}/posts/${newPostKey}`), {
        content: blogContent,
        user: uid,
        timestamp: Date.now(),
        postKey: newPostKey,
        media: mediaURL,
        mediaType: mediaType, 
      });

      
  
      toast.success('Blog posted Successfully, Please Refresh for changes');
    }
  
    navigate('/home');
  };
  



  return (
    <>
    {loading && (
        <div className="loading-overlay">
          <div className="loading-spinner"></div>
        </div>
      )}
      <Header />

      <p className='post-para'>Post what is on your mind!</p>


    <div className='create-post-area'>
      <textarea name="blog" id="" cols="30" rows="3" placeholder='What is happening in the Campus?...' value={blogContent} onChange={handleBlog}></textarea>
      <img src={mediaURL} alt="" className='post-media'/>
      { mediaURL && <p className='media-confirm'>{file.name} is uploaded</p>}
      <div className="buttons-post-area">
      <label className="input-wrapper">
        <input type="file" onChange ={handleImage} />
        <span className="custom-file-upload">{mediaURL ? 'Selected!' : 'Media'}</span>
      </label>
        <button onClick={handlePostDB} className='submit-post'>Post</button>
      </div>
    </div>
    <Footer/>
    </>
  )
}


export default PostCreateButton