import React, { useEffect, useRef, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import Header from '../Components/Header';
import NavBar from '../Components/NavBar';
import likeSound from '../Components/like.mp3';
import { get, ref, update, remove, push , set } from 'firebase/database';
import { db } from '../firebase';
import { toast } from 'react-toastify';
import UserInstantDetails from '../Components/UserInstantDetails';
import AddComment from '../Components/AddComment';
import Comment from '../Components/Comment';
import Comment2 from '../Components/Comment2';

const PostPage = () => {
  const [loading, setLoading] = useState(true);
  const [doubleTap, setDoubleTap] = useState(false);
  const [post, setPost] = useState({});
  const [likeCount, setLikeCount] = useState(0);
  const [likedUser, setLikedUser] = useState([]);
  const [user, setUser] = useState({});
  const [selectedUserId, setSelectedUserId] = useState('');
  const [instantInfo, showInstantInfo] = useState(false);
  const [uid, setUid] = useState('');
  const [comments, setComments] = useState([]);
  const [comment, setComment] = useState('');
  const audioRef = useRef(null);
  const [realComments, setRealComments] = useState([]);
  const navigate = useNavigate();
  const { postkey } = useParams();
  const [lengthComments, setLengthComment] = useState(0);
  const [commentUser, setCommentUser] = useState({});

  useEffect(() => {
    const postRef = ref(db, `posts/${postkey}/`);
    get(postRef).then((snapshot) => {
      if (snapshot.exists()) {
        setPost(snapshot.val());
      }
    });

    const userInfo = ref(db, `users/${post?.user}`);
    get(userInfo).then((snapshot) => {
      if (snapshot.exists()) {
        setUser(snapshot.val());
      }
    });

    const postLikesRef = ref(db, `posts/${postkey}/likes`);
    get(postLikesRef).then((snapshot) => {
      if (snapshot.exists()) {
        const likes = snapshot.val();
        setLikedUser(Object.keys(likes));
        setLikeCount(Object.keys(likes).length);
      }
    });

    setUid(localStorage.getItem('uid'));
  }, [postkey, post?.user]);

  useEffect(() => {
    setLoading(true);

    const commentRef = ref(db, `posts/${postkey}/comments`);
    get(commentRef)
      .then((snapshot) => {
        if (snapshot.exists()) {
          const commentList = snapshot.val();
          setRealComments(commentList);
          const reversedComments = commentList ? Object.values(commentList).reverse() : [];
          setComments(reversedComments);

          const lengthOfComments = reversedComments.length;
          setLengthComment(lengthOfComments);
        } else {
          setComments([]);
          setLengthComment(0);
        }
      })
      .finally(() => {
        setLoading(false);
      });
  }, [postkey]);

  const handleDoubleTap = () => {
    if (doubleTap) {
      setDoubleTap(false);
      handleLike();
    } else {
      setDoubleTap(true);
      setTimeout(() => {
        setDoubleTap(false);
      }, 300);
    }
  };

  const handleLike = () => {
    const notiRef = ref(db, `notification/${post?.user}/`)
    const updates = {};
    if (likedUser.includes(uid)) {
      updates[`posts/${postkey}/likes/${uid}`] = null;
    } else {
      updates[`posts/${postkey}/likes/${uid}`] = true;
    }

    update(ref(db), updates)
      .then(() => {
        const updatedLikedUser = likedUser.includes(uid)
          ? likedUser.filter((userId) => userId !== uid)
          : [...likedUser, uid];
          if (!likedUser.includes(uid)){push(notiRef,{
            uid: uid,
            message: "liked your post",
            read : false,
            post: post?.postKey,
            timestamp: Date.now()
          })}
        setLikedUser(updatedLikedUser);
        setLikeCount(updatedLikedUser.length);
      })
      .catch((error) => {
        console.error('Error updating like: ', error);
      });
    audioRef.current.play();
  };

  const handleDelete = () => {
    const postRef = ref(db, `posts/${postkey}`);
    const userConfirmation = window.confirm('Are you sure you want to delete this post?');
    if (userConfirmation) {
      remove(postRef).then(() => {
        window.location.reload();
        toast.success('Post is deleted successfully!');
      });
    }
  };

  const handleInstantInfo = () => {
    setSelectedUserId(user?.uid);
    showInstantInfo(true);
  };

  const formattedContent = post?.content ? post.content.replace(/\n/g, '<br>') : '';

  const handleCommentInput = (e) => {
    e.preventDefault();
    setComment(e.target.value);
  };

  const handleCommentUpload = async () => {
    const notiRef = ref(db, `notification/${post?.user}`)
    const commentRef = ref(db, `posts/${postkey}/comments/`);
    const uid = localStorage.getItem('uid');

    const newCommentRef = push(commentRef, {
      content: comment,
      user: uid,
      timestamp: Date.now()
    });
    
    const commentKey = newCommentRef.key;

    await set(ref(db, `posts/${postkey}/comments/${commentKey}/`), {
      content: comment,
      user: uid,
      key: commentKey,
      timestamp: Date.now()
    });

    const notification = push(notiRef,{
      uid: uid,
      message: "commented on your post",
      comment : comment,
      read: false,
      post: postkey,
      commentID : commentKey,
      timestamp: Date.now()
    })
    const notiKey = notification.key

    await set(ref(db,`notification/${post?.user}/${notiKey}`),{
      uid: uid,
      message: "commented on your post",
      comment : comment,
      read: false,
      post: postkey,
      commentID : commentKey,
      timestamp: Date.now(),
      key : notiKey
    })


    toast.success('Comment uploaded');
    window.location.reload();
  };

  return (
    <div className='post-page'>
      {loading && (
        <div className='loading-overlay'>
          <div className='loading-spinner'></div>
        </div>
      )}
      <Header />
      <div className='body-container'>
        {instantInfo && selectedUserId && <UserInstantDetails instant={showInstantInfo} userId={selectedUserId} />}

        <NavBar />
        <div className='post-box-page' onClick={handleDoubleTap}>
          <audio ref={audioRef} src={likeSound} />
          <div className='post-user'>
            <img src='https://cdn-icons-png.flaticon.com/128/507/507257.png' className='back-btn' alt='' onClick={() => window.location.href = `/home`} />
            <img src={user?.profiles?.profileURL} alt='' className='post-user-profile' onClick={handleInstantInfo} />
            <div className='user-details-post' onClick={handleInstantInfo}>
              <h1 onClick={handleInstantInfo}>{user?.profilename}</h1>
              <p>@{user?.username}</p>
            </div>
            <button onClick={() => navigate(`/profile/${user.uid}`)}>View</button>
          </div>
          <div className='post-content' dangerouslySetInnerHTML={{ __html: formattedContent }} />
          {post?.media && (
    <img src={post.media} alt="" className="post-media" onClick={()=> window.location.href = (`post/${post?.postKey}`)}/>
  )}

      {post?.mediaType === 'video' && post?.media && (
        <video controls className="post-media" onClick={()=> window.location.href = (`post/${post?.postKey}`)}>
          <source src={post.media} type="video/mp4" />
          Your browser does not support the video tag.
        </video>
      )}          <div className='post-status'>
            <div className='post-function'>
              <img
                src={
                  likedUser.includes(uid)
                    ? 'https://icones.pro/wp-content/uploads/2021/02/icone-de-coeur-bleu-1.png'
                    : 'https://cdn-icons-png.flaticon.com/128/1077/1077035.png'
                }
                alt=''
                onClick={handleLike}
              />
              <p>{likeCount}</p>
            </div>
            <div className='post-function'>
              <img src='https://cdn-icons-png.flaticon.com/128/2462/2462719.png' alt='' onClick={() => {console.log(comments)}} />
              <p>{lengthComments}</p>
            </div>
            <div className='post-function'>
              <img src='https://cdn-icons-png.flaticon.com/128/2958/2958783.png' alt='' />
            </div>
            <div className='post-function'>
              <img src='https://cdn-icons-png.flaticon.com/128/9511/9511721.png' alt='' />
            </div>
            {user?.uid === uid && (
              <div className='post-function' onClick={handleDelete}>
                <img src='https://cdn-icons-png.flaticon.com/128/6861/6861362.png' alt='' />
              </div>
            )}
          </div>

          <div className='comment-container'>
            <div className='comment-input'>
              <input type='text' placeholder='add comment...' onChange={handleCommentInput} />
              <img src='https://cdn-icons-png.flaticon.com/128/10322/10322482.png' onClick={handleCommentUpload} alt='' />
            </div>
            {lengthComments === 0 &&
            <p style={{textAlign:'center', padding:'10px', color:'white'}} >Be the first person to comment </p>
            }
            {Object.values(comments).map((com) =>(<Comment2 com={com} id={com} postkey={postkey}post={post} />)) }
          </div>
        </div>
      </div>
    </div>
  );
};

export default PostPage;
