import React,{useState, useEffect} from 'react'
import { db } from '../firebase'
import { ref, get } from 'firebase/database'
import { useNavigate } from 'react-router-dom'

const SearchItem = ({uid, searchValue}) => {

    const navigate = useNavigate();


    const [username, setUsername] = useState("");
    const [profileName, setProfileName] = useState("");
    const [profileURL , setProfileURL ] = useState("");
    const [loading , setLoading ] = useState(true);
    

    useEffect(() => {
        // Create references for each data point
        const usernameRef = ref(db, `users/${uid}/username`);
        const profileNameRef = ref(db, `users/${uid}/profilename`);
        const profileURLRef = ref(db, `users/${uid}/profiles/profileURL`);
    
        // Fetch data for each reference
        get(usernameRef)
          .then((usernameSnapshot) => {
            if (usernameSnapshot.exists()) {
              setUsername(usernameSnapshot.val());
            }
          })
          .catch((error) => {
            console.error('Error fetching username:', error);
          });
    
        get(profileNameRef)
          .then((profileNameSnapshot) => {
            if (profileNameSnapshot.exists()) {
              setProfileName(profileNameSnapshot.val());
            }
          })
          .catch((error) => {
            console.error('Error fetching profile name:', error);
          });
    
        get(profileURLRef)
          .then((profileURLSnapshot) => {
            if (profileURLSnapshot.exists()) {
              setProfileURL(profileURLSnapshot.val());
            }
          })
          .catch((error) => {
            console.error('Error fetching profile URL:', error);
          })
          .finally(() => {
            setLoading(false); // Set loading to false when data fetching is complete
          });
      }, [uid]);

      const searchUsername = username;
      const shouldRender = searchUsername.toLowerCase().includes(searchValue.toLowerCase());


  return (
   shouldRender && (<div className="search-user" onClick={()=>{window.location.href = (`profile/${uid}`)}}>
        {loading && (
                <div className="loading-overlay">
                    <div className="loading-spinner"></div>
                </div>
            )}
            <img src={profileURL ? profileURL : 'https://cdn-icons-png.flaticon.com/128/149/149071.png'} alt="" className='search-user-profile' />
            <div className="user-details-search">
              <h1>{profileName}</h1>
              <p>{username}</p>
            </div>
    </div>) 
  )
}

export default SearchItem