import { get, ref } from 'firebase/database';
import React, { useEffect, useState } from 'react'
import { db } from '../firebase';
import { useNavigate } from 'react-router-dom';

const FollowItem = ({uid}) => {
    const [user, setUser] = useState([]);
    const navigate = useNavigate()
    const [loading, setLoading] = useState(false)


    useEffect(()=>{
        setLoading(true)
        const userRef = ref(db, `users/${uid}`)

        get(userRef)
        .then((snapshot)=>{
            if(snapshot.exists()){
                setUser(snapshot.val())
            }
        })
        setLoading(false)
    })

    const handleUserShow = () =>{
      window.location.href = `profile/${uid}`
    }



  return (
    <div className="search-user" onClick={handleUserShow}>
    {loading && (
        <div className="loading-overlay">
            <div className="loading-spinner"></div>
        </div>
    )}
    <img src={user?.profiles?.profileURL} alt="" className='search-user-profile' />
    <div className="user-details-search">
      <h1>{user?.profilename}</h1>
      <p>{user?.username}</p>
    </div>

</div>
  )
}

export default FollowItem