import React, { useState } from 'react';
import '../ComponentsCSS/Loginbox.css';
import { auth, db } from '../firebase';
import { signInWithEmailAndPassword } from 'firebase/auth';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useNavigate } from 'react-router-dom';
import { get } from 'firebase/database';
import { push, ref, set } from 'firebase/database';

const LoginBox = () => {
  const [userData, setUserData] = useState({})
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  const handleSubmit = (e) => {
    setLoading(true);
    e.preventDefault();
    const email = e.target.email.value;
    const password = e.target.password.value;

    if (!email.includes('@veltechmultitech.org')) {
      toast.error('Error Logging In: Email must be a @veltechmultitech.org address.');
      setLoading(false);
      return;
    }

    signInWithEmailAndPassword(auth, email, password)
      .then((userCredential) => {
        const user = userCredential.user;
        const usernameRef = ref(db, `/users/${user.uid}/username`);
        const userRef = ref(db, `/users/${user.uid}/`);

        get(userRef)
        .then((snapshot)=>{
          if(snapshot.exists()){
            const userIn = snapshot.val()
            console.log(userIn)
            localStorage.setItem('profile_pic', userIn?.profiles?.profileURL);

          }
        })

        get(usernameRef)
          .then((snapshot) => {
            if (snapshot.exists()) {
              const username = snapshot.val();
              localStorage.setItem('username', username);
              localStorage.setItem('uid', user.uid);
              localStorage.setItem('email', user.email);
              toast.success(`Welcoming back my friend! ${username}`);
              navigate(`/profile/${user.uid}`);
            } else {
              toast.error('No username found');
            }
          })
          .catch((error) => {
            const errorCode = error.code;
            const errorMessage = error.message;
            toast.error('Enter correct values', errorCode, errorMessage);
          })
          .finally(() => {
            setLoading(false);
          });
      })
      .catch((error) => {
        const errorCode = error.code;
        const errorMessage = error.message;
        toast.error('Enter correct values', errorCode, errorMessage);
        setLoading(false);
      });
  };

  return (
    <form className='login-box' onSubmit={(e) => handleSubmit(e)}>
      <h1>Login:</h1>
      <input name='email' type="text" placeholder='Veltech email id...' className='email' />
      <input name='password' type="password" placeholder='Password...' className='password' />
      <button>Login</button>
    </form>
  );
};

export default LoginBox;
